.backdrop {
   position: fixed;
   top: 0;
   left: 0;
   width: 100%;
   height: 100%;
   background-color: $buttons-light-color;
   opacity: 1;
   visibility: visible;
   transition: visibility $cubic,
      opacity $cubic;
   overflow: scroll;

   &.is-hidden {
      visibility: hidden;
      opacity: 0;
      pointer-events: none;
   }
}

.modal-price {
   display: block;
   position: absolute;
   top: 10px;
   left: 50%;
   padding: 60px 15px;
   transform: translateX(-50%) scale(1);
   opacity: 1;
   width: 100%;
   max-width: 414px;
   background: $background-light-color;
   border-radius: 2px;

   @include tablet {
      max-width: 712px;
      padding: 60px;
   }

}

.modal-price__title {
   margin-bottom: 40px;
   font-family: 'Times New Roman', serif;
   font-style: normal;
   font-weight: 700;
   font-size: 24px;
   line-height: 1.167;
   letter-spacing: 0.05em;
   color: $main-dark-color;
   text-transform: uppercase;
   text-align: center;

   @include tablet {
      font-size: 32px;
      line-height: 1.157;

   }
}

.modal-price__subtitle {
   margin-bottom: 28px;
   font-family: 'Times New Roman', serif;
   font-style: normal;
   font-weight: 700;
   font-size: 24px;
   line-height: 1.167;
   letter-spacing: 0.05em;
   color: $modal-placeholder;
}

.modal-price__text {
   margin-bottom: 20px;
   font-weight: 500;
   font-size: 16px;
   line-height: 1.25;
   letter-spacing: 0.02em;
   color: $second-accent-color;
}

.modal-price__list {
   display: flex;
   flex-direction: column;
   gap: 20px;

   @include tablet {
      flex-direction: row;
   }
}

.modal-price__item {
   display: flex;
   flex-direction: column;
}

.modal-price__item:last-child {
   margin-bottom: 40px;
}

.modal-price__input {
   width: 100%;
   height: 54px;
   margin-bottom: 8px;
   border: 1px solid $second-accent-color;
   background-color: $background-light-color;
   font-family: 'Times New Roman';
   font-style: normal;
   font-weight: 700;
   font-size: 24px;
   line-height: 1.167;
   letter-spacing: 0.05em;
   color: $modal-placeholder;
   outline: none;

   &--border {
      border: none;
   }

   @include tablet {
      width: 288px;
   }
}

.modal-price__label {
   font-weight: 500;
   font-size: 16px;
   line-height: 1.25;
   letter-spacing: 0.02em;
   color: $second-accent-color;

   @include tablet {
      margin-left: 12px;
   }

   &--lowercase {
      text-transform: lowercase;
   }
}

.modal-price__button {
   background-color: $main-dark-color;
   color: $main-light-color;
   width: 100%;
   padding: 20px 64px;
   border: 1px solid $accent-color;
   border-radius: 2px;
   font-family: inherit;
   font-weight: 500;
   font-size: 18px;
   line-height: 1.2;
   letter-spacing: 0.02em;
   text-transform: uppercase;
   cursor: pointer;
   transition: background-color $cubic;
   transition: color $cubic;

   @include tablet {
      font-size: 24px;
   }

   &:hover,
   &:focus {
      background-color: $accent-color;
      color: $main-dark-color;
   }
}

.modal-price__btn {
   position: absolute;
   top: 16px;
   right: 16px;
   display: flex;
   justify-content: center;
   align-items: center;
   padding: 0;
   width: 24px;
   height: 24px;
   background: transparent;
   border: none;
   cursor: pointer;
}